
import { defineComponent } from 'vue'
import AppModal from './AppModal.vue'
import BaseButton from './BaseButton.vue'

export default defineComponent({
  name: 'ModalButton',
  components: { BaseButton, AppModal },
  props: {
    actionLabel: {
      type: String,
      default: 'Confirm'
    },

    header: {
      type: String,
      required: true
    },

    message: {
      type: String,
      required: true
    },

    prominent: {
      type: Boolean,
      default: false
    }
  },
  emits: ['confirm'],
  data () {
    return {
      modalOpen: false
    }
  }
})
